import React, { Component } from 'react'
import ServicesElement from "./Elements/ServicesElement"
import Modal from './Modal';
import Footer from "./Footer";

export class Services extends Component {
  state={
      openModal: false
  }
  openModal = (e) =>{
    this.setState({
        openModal: !this.state.openModal
    })
  }
  render() {
    const {data} = this.props
    return (
      <div className="Services" id="services">
          <h1 className="ServicesTitle">{data.title}</h1>
          <div className="ServicesElement">
          {
              data.ServicesElement.map((element)=>{
                  return(
                      <ServicesElement data ={element}></ServicesElement>
                  )
              })
          }
          </div>
          <button onClick={this.openModal} className="ServicesButton">{data.callToAction}</button>
          {
              this.state.openModal &&
              <Modal data="¿Cómo deseas contactarme?" handleOpenModal={this.openModal}>
                <Footer></Footer>
              </Modal>
          }
      </div>
    )
  }
}

export default Services
